import React from 'react';
import './header.scss';
import utility from '../utility';

class Header extends React.Component{
    
  componentDidMount() {
    utility.clock();
    document.getElementById('date').innerHTML = (new Date()).toLocaleDateString('en-US', { weekday: 'long', year: 'numeric', month: 'long', day: 'numeric' });
  }

  render(){
    return(
      <div className="header">
        <section id="header">
          <div class="top-menu-outer">
            <div class="top-menu region"><span id="date"></span><span id="kh-clock" class="time">time</span></div>
          </div>
          <div class="title-outer">
            <h1 class="site-title region">
              <span><a href="/" >Multimedia</a></span>
              <img class="news" alt="" src={'/images/newspaper.png'} />
              <span><img alt="" class="tvlive" src={'/images/livetv.png'}/></span>
            </h1>
          </div>
        </section>
      </div>
    );
  }
}

export default Header;
